<template>

<div class="black-bg" v-if="모달창열렸니 == true">
  <div class="white-bg">
    <img :src="원룸들[누른거].image" style="width: 100%">
    <h4>{{ 원룸들[누른거].title}}</h4>
    <p>{{ 원룸들[누른거].content}}</p>
    <select v-model="month">
        <option v-for="a in 20" :key="a">
            {{a}}
        </option>
    </select>
    <input v-model.number="month">
    <p> {{month}} 개월 선택 함 : {{ 원룸들[누른거].price * month}}원</p>
    <button @click="$emit('closeModal')">닫기</button>
  </div>
</div>

</template>

<script>
export default {
  name: 'Modal-a',

    data(){
        return{
            month:1,
        }
    },
    watch:{
        month(af_val){
            if(isNaN(af_val) == true)
            {
                alert('문자입력 하지 마세요.')
                this.month = 1;
            }
            if(af_val > 13){
                alert('13이 입력하지 마세요.')
            }
        },
    },
    beforeUpdate(){
        if(this.month == 2){
            alert('2개월은 너무 적음')
            this.month = 0;
        }
    },
    props : {
        원룸들 : Array,
        누른거 : Number,
        모달창열렸니 : Boolean,
    }
}
</script>

