<template>

<!-- 모달 생성 -->
<transition name ="fade">
<nm_Modal @closeModal="모달창열렸니 = false" :원룸들="원룸들" :누른거="누른거" :모달창열렸니="모달창열렸니"/>
</transition>

<!-- 메뉴 -->
<div class="menu">
  <a v-for="a in 메뉴들" :key="a">{{ a }}</a>
</div>

<!-- 할인 배너 -->
<nm_DisCount v-if="showDiscount == true" :할인률="할인률"/>

<!-- 정렬 기능 -->
<button @click="priceSort">가격순정렬</button>
<button @click="priceSortBack">정렬 되돌리기</button>

<!-- 메인화면 -->
<nm_Card @openModal="모달창열렸니 = true; 누른거 = $event" :원룸="원룸들[i]" v-for="(a, i) in 원룸들" :key="i"/>

</template>




<script>
import onerooms from './assets/oneroom';
import Discounts from './Discount.vue';
import Modals from './Modal.vue';
import Cards from './Card.vue';


export default {
  name: 'App',
  data(){
    return{
      showDiscount : true,
      원룸들오리지널 : [...onerooms],
      오브젝트 : { name :'kim', age :20 },
      모달창열렸니: false,
      원룸들 : onerooms,
      누른거 : 0,
      할인률 : 30,

      메뉴들: ['Home','Shop','About'],
    }
  },

  methods: {
    priceSort(){
      this.원룸들.sort(function(a,b){
        return a.price - b.price
      })
    },
    priceSortBack(){
      this.원룸들 = [...this.원룸들오리지널]
    }
  },

  /* 
     30초후 Discount 사라지게 하기 
     1초마다 Discount 값 변경 하기
  */
  mounted(){
    setTimeout(()=>{
      this.showDiscount = false;
    }, 30000),

    setInterval(() => {
      this.할인률--;
    }, 1000)
  },

  components: {
    nm_DisCount : Discounts,
    nm_Modal : Modals,
    nm_Card : Cards,
  }
}
</script>




<style>
/* 등장 애니메이션 (transition)
   작명-enter-from
   작명-enter-active
   작명-enter-to

   퇴장 애니메이션
   작명-leave-from
   작명-leave-active
   작명-leave-to
   위 3개는 필수 (현재는 작명 Name을 fade로해서 fade로 함)
 */

 /* ***등장*** */
/* 시작시 스타일 */
.fade-enter-from{ 
 /*  opacity: 0; /* 투명도 */
 transform: translateY(-1000px);
} 

.fade-enter-active{
  transition: all 1s;
}
/* 끝날시 스타일 */
.fade-enter-to{
 /*  opacity: 1;  */
  transform: translateY(0px);
}

 /* ***퇴장*** */
/* 시작시 스타일 */
.fade-leave-from{ 
  opacity: 1; /* 투명도 */
} 

.fade-leave-active{
  transition: all 1s;
}
/* 끝날시 스타일 */
.fade-leave-to{
  opacity: 0; /* 투명도 */
}

